.master-canvas-wrap, .master-canvas-inner-wrap {
  position: relative; }

#site-logo {
  max-width: 162px; }

@media only screen and (min-width:40.063em) {
  #site-logo {
    max-width: 180px; } }

#page-header {
  position: relative;
  height: 480px; }
  @media only screen and (min-width:40.063em) {
    #page-header {
      height: 540px; } }
  @media only screen and (min-width:64.063em) {
    #page-header {
      height: 600px; } }

.top-bar-container {
  position: relative; }

.top-bar .name {
  background-color: rgba(0, 0, 0, 0.1); }

.two-row-menu {
  line-height: 2.5rem !important;
  height: 2.5rem !important; }

.header-icon {
  width: 20px;
  fill: transparent;
  stroke: #FFFFFF;
  vertical-align: middle; }

.header-icon.icon-telephone {
  stroke-width: 2.4px; }

.header-icon.icon-close-email {
  stroke-width: 0.5px; }

.wall-dark-layer {
  background-color: rgba(0, 0, 0, 0.2); }

.wall-dark-layer, .wall-carousel {
  position: absolute;
  height: 100%;
  width: 100%;
  left: 0;
  top: 0;
  z-index: -1; }

.wall-background {
  width: 100%;
  height: 100%;
  background-size: cover;
  background-position: center center;
  background-attachment: fixed;
  background-repeat: no-repeat; }

.wall-background-index-0 {
  background-image: url("/img/walls/kanchanjanga-from-darjeeling-1600x1000.jpg"); }

.wall-background-index-1 {
  background-image: url("/img/walls/lord-shankar-at-namchi-1600x1000.jpg"); }

.wall-background-index-2 {
  background-image: url("/img/walls/agra-fort-india-1600x1000.jpg"); }

.wall-carousel .slick-list, .wall-carousel .slick-track {
  height: 100%; }

.content-panel {
  padding: 0 1rem; }

.master-breadcrumb li {
  display: inline;
  float: none; }

.tag-line-container {
  letter-spacing: 1px;
  position: absolute;
  z-index: 1;
  top: 40%;
  width: 100%; }
  .tag-line-container .tag-line {
    color: #FFFFFF;
    font-size: 3rem; }
  .tag-line-container .tag-line-sub {
    color: #FFFFFF;
    font-size: 1.3125rem; }

@media only screen and (min-width:40.063em) {
  .tag-line-container {
    top: 55%; } }

#page-footer {
  padding: 5% 0 1.5% 0;
  background-color: #006692;
  color: #FFFFFF; }
  #page-footer ul {
    list-style: none;
    margin: 0; }
  #page-footer h2, #page-footer a {
    color: inherit; }
  #page-footer .address-block, #page-footer .logo-block {
    margin-top: 20px; }

.footer-icons {
  stroke: white;
  fill: none;
  float: left;
  display: block;
  width: 30px;
  margin-right: 10px; }

.icon-facebook {
  stroke-width: 5px; }

.icon-twitter {
  stroke-width: 25px; }

.footer-logo {
  margin-top: 10px;
  display: block;
  max-width: 180px; }

@media only screen and (min-width:64.063em) {
  #page-footer {
    padding-top: 50px; }
    #page-footer .address-block, #page-footer .logo-block {
      margin-top: 0; } }

